.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
/* Arachne */
nav {
  position: fixed;
  bottom: 0;
  width: 100%;
  background-color: #333;
  display: flex;
  justify-content: space-around;
  align-items: center;
  box-shadow: 0px -3px 10px rgba(0, 0, 0, 0.2); /* Add a shadow to the bottom */
}

nav button {
  width: 33%;
  background-color: #555;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

nav button:hover {
  background-color: #777;
}

#taskFormSimple {
  justify-content:center;
}

.Submit_or_Cancel_container {
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
}

#modal {
  display: none; /* Hide modal by default */
  position: fixed; /* Position modal fixed to the viewport */
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5); /* Semi-transparent background overlay */
  z-index: 9999; /* Ensure modal appears above other content */
  overflow: auto; /* Enable scrolling if content exceeds viewport */
}

#modal.active {
  display: block; /* Show modal when it has the 'active' class */
}

#modal-content {
  background-color: #fefefe;
  margin: 15% auto; /* Center modal vertically and horizontally */
  padding: 20px;
  border: 1px solid #888;
  border-radius: 5px;
  width: 80%; /* Adjust width as needed */
  max-width: 600px; /* Set maximum width for larger screens */
}
.card.modal-card-for-form {
  margin: 15% auto; /* Center modal vertically and horizontally */
  max-width: 25rem;
}